/*============================ 
// Global styles
=============================*/
* {
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

body {
  scroll-behavior: smooth;
  margin: 0;
  padding: 0;
  font-family: var(--font-body);
  font-size: 1rem;
}

ol {
  padding-left: 1rem;
}
ol li {
  padding: 0;
  margin: 0;
  list-style: decimal;
}

ul,
li {
  padding: 0;
  margin: 0;
  list-style: none;
}

a {
  display: inline-block;
  transition: all 0.4s ease-in-out;
  text-decoration: none;
}
a:link, a:visited, a:focus {
  text-decoration: none;
  outline: 0;
}
a:hover {
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
}

input:focus,
textarea:focus,
select:focus {
  outline: 0;
  box-shadow: none;
}

.no-padding {
  padding-left: 0 !important;
  padding-right: 0 !important;
}
.no-padding__left {
  padding-left: 0 !important;
}
.no-padding__right {
  padding-right: 0 !important;
}

section,
.section {
  position: relative;
  overflow: hidden;
}

@keyframes toBottomFromTop {
  49% {
    transform: translateY(-100%);
  }
  50% {
    opacity: 0;
    transform: translateY(100%);
  }
  51% {
    opacity: 1;
  }
}
@media (min-width: 576px) {
  .container {
    max-width: 440px;
  }
}
@media (min-width: 768px) {
  .container {
    max-width: 540px;
  }
}
@media (min-width: 992px) {
  .container {
    max-width: 720px;
  }
}
@media (min-width: 1200px) {
  .container {
    max-width: 960px;
  }
}
@media (min-width: 1400px) {
  .container {
    max-width: 1170px;
  }
}
