.button {
  font-size: 1rem;
  font-weight: 500;
  line-height: 19px;
  text-decoration: none;
  text-transform: capitalize;
  cursor: pointer;
  border-radius: 500px;
  padding: 20px 30px;
}

.button-primary {
  align-items: center;
  background: var(--color-secondary);
  color: var(--color-white);
  overflow: hidden;
}
.button-transparent {
  background: transparent;
  position: relative;
  overflow: hidden;
  border: 1px solid #1E1E2F;
  color: #1E1E2F;
}
.button-transparent svg {
  transition: all 0.3s ease;
}
.button-transparent::after {
  background: var(--color-white);
  content: "";
  height: 155px;
  left: -75px;
  opacity: 0.2;
  position: absolute;
  top: -50px;
  transform: rotate(35deg);
  transition: all 550ms cubic-bezier(0.19, 1, 0.22, 1);
  width: 50px;
  z-index: -10;
}
.button-transparent:hover {
  background: var(--color-primary);
  color: var(--color-white);
  border: 1px solid var(--color-primary);
}
.button-transparent:hover svg {
  fill: var(--color-white);
  transform: rotate(45deg);
}
.button-transparent:hover::after {
  left: 120%;
  transition: all 550ms cubic-bezier(0.19, 1, 0.22, 1);
}

.button-white {
  display: inline-flex;
  align-items: center;
  gap: 10px;
  background: var(--color-white);
  color: var(--color-primary);
  position: relative;
  overflow: hidden;
  z-index: 1;
}
.button-white svg {
  transition: all 0.4s ease;
}
.button-white:hover {
  color: var(--color-white);
  background: var(--color-secondary);
}
.button-white:hover svg {
  fill: var(--color-white);
}

.e-button {
  display: inline-block;
  position: relative;
  padding: 10px;
  overflow: hidden;
}
.e-button__link {
  text-decoration: none;
  color: #fff;
  font-family: Helvetica;
  font-size: 30px;
  text-transform: uppercase;
  display: block;
  text-align: center;
  position: relative;
  padding: 10px 5px;
  border: 3px solid #fff;
}
.e-button .line {
  position: absolute;
  background-color: #fff;
}
.e-button .line--one {
  width: 1px;
  height: 100%;
  left: 0;
  top: 0;
  transform: translate3d(0px, 102%, 0px);
}
.e-button .line--two {
  width: 100%;
  height: 1px;
  left: 0;
  top: 0;
  transform: translate3d(-102%, 0px, 0px);
}
.e-button .line--three {
  width: 1px;
  height: 100%;
  right: 0;
  top: 0;
  transform: translate3d(0px, -102%, 0px);
}
.e-button .line--four {
  width: 100%;
  height: 1px;
  left: 0;
  bottom: 0;
  transform: translate3d(102%, 0px, 0px);
}
.e-button:hover .line {
  opacity: 1;
}
.e-button:hover .line--one, .e-button:hover .line--two, .e-button:hover .line--three, .e-button:hover .line--four {
  transform: translate3d(0, 0, 0);
  transition: transform 0.3s ease-in-out;
}
.e-button:hover .line--one {
  transition-delay: 0s;
}
.e-button:hover .line--two {
  transition-delay: 250ms;
}
.e-button:hover .line--three {
  transition-delay: 500ms;
}
.e-button:hover .line--four {
  transition-delay: 750ms;
}

