#preloader {
  position: fixed;
  background: #FFFFFF;
  width: 100%;
  height: 100%;
  overflow: visible;
  left: 0;
  top: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 9999999;
}
#preloader .loading-wrapper {
  width: 180px;
  height: 180px;
  position: absolute;
  top: calc(50% - 100px);
  left: calc(50% - 100px);
  display: flex;
  font-family: Helvetica;
  flex-direction: column;
  justify-content: center;
}
#preloader .loading-text {
  text-align: center;
}
#preloader .circle {
  box-sizing: border-box;
  border-top: 7px solid var(--color-secondary);
  border-bottom: 7px solid transparent;
  border-right: 7px solid transparent;
  border-left: 7px solid transparent;
  border-radius: 100%;
  position: relative;
}
#preloader .circle::before {
  content: "";
  position: absolute;
  width: 8px;
  height: 8px;
  background: var(--color-secondary);
  left: 19px;
  top: 17px;
  border-radius: 100px;
}
#preloader .circle::after {
  content: "";
  position: absolute;
  width: 8px;
  height: 8px;
  background: var(--color-secondary);
  right: 19px;
  top: 17px;
  border-radius: 100px;
}
#preloader .loading-circle {
  height: 100%;
  width: 100%;
  position: absolute;
  animation: rotate 1.5s infinite;
}
